.footer {
    background-color: var(--dark-blue);
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    min-height: 15vh;
  }
  
  .footerLeft {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 14px;
  }
  
  .footerRight {
    display: flex;
    align-items: center;
    font-size: 14px;
  }
  
  .whiteLogo {
    width: 250px;
    margin-bottom: 20px;
  }