.header {
    display: flex;
    flex-direction: column;
  }
  
  .headerTop {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
  }
  
  .logo {
    width: 200px;
    margin-right: -90px;
  }
  
  .phone {
    flex-grow: 1;
    text-align: center;
    font-family: PT-Astra-Sans_Bold, sans-serif;;
    font-size: 18px;
    opacity: 0.7;
  }
  
  .authButton {
    background-color: var(--bright-red);
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    width: 200px;
    height: 50px;
    font-size: 18px;
    transition: opacity 0.3s;
  }

  .authButton:hover {
    opacity: 0.8;
  }
  
  .headerBottom {
    width: 100%;
    text-align: left;
    margin-top: 5px;
    opacity: 0.5;
  }