:root {
  --dark-blue: #163E6C;
  --bright-red: #D20A11;
  --cream: #EBE6D6;
}


body, html {
  margin: 0;
  padding: 0;
  font-family: PT-Astra-Sans_Regular, sans-serif;
}

header {
  background-color: var(--cream);
  padding: 10px 20px;
  text-align: center;
  min-height: 8vh;
}

footer {
  background-color: var(--dark-blue);
  color: white;
  padding: 10px 20px;
  text-align: center;
  min-height: 15vh;
}

main {
  padding: 20px;
  min-height: 70vh;
}

